.auditPartners {
       color: yellow;
       text-decoration: solid !important;
       background-color: blue;
}


input {
       color: black !important;
}

span {
       font-size: 19px !important;
}

.addAuditPartner {
       display: none;
       height: 350px;
       border: 1px solid white;
       align-items: center;
}

.right {
       width: 50%;
       height: 55%;
       display: flex;
       flex-direction: column;
       justify-content: space-between;
       padding-left: 130px;
}

.right span {
       font-size: 18px;
}

.right input {
       outline: none;
       width: 320px;
       height: 35px;
       padding-left: 5px;
}

.right span {
       color: white;
}

.right div {
       height: 32%;
}

.right .bio {
       height: 36%;
}

.right .bio textarea {  
       height: 45px;
       width: 350px;
       color: black;
       font-size: 14px;
       padding-left: 5px;
}

.left {
       width: 50%;
       height: 55%; 
       display: flex;
       flex-direction: column;
       justify-content: space-between;
}


.services{
       display: flex; 
       display: flex;
       justify-content: space-between;
       width: 90%;
       
}

.socials {
       display: flex;
       justify-content: space-between;
       width: 90%;
}

.socials input{
       height: 30px;
       width: 155px !important;

}


.see {
       position: absolute;
       top: 25%;
       left: 28%;
       background-color: red;
}

.submit {
       border: 1px solid white;
       height: 90px !important;
       margin-top: 41px;
       margin-left: 520px;
}

.submit:hover {
       background-color: rgb(0, 17, 248);
       transition: all .2s ease-in-out;
}